html,
body {
  height: 100%; }

body {
  display: flex;
  font-size: 24px;
  line-height: 150%;
  margin: 0;
  text-align: center; }

.main-wrapper {
  background-size: cover;
  background-attachment: fixed;
  display: flex;
  flex-grow: 1;
  padding: 75px 0; }

.page-wrapper {
  align-items: center;
  display: flex;
  flex: 1 0 100%;
  justify-content: center; }

.button--ghost {
  background-color: transparent;
  border: 1px solid #f5f5f5;
  color: #f5f5f5;
  cursor: pointer;
  font-family: sans-serif;
  font-size: 16px;
  margin-top: 50px;
  opacity: 0.6;
  padding: 10px 15px;
  text-transform: uppercase; }

h1,
h2,
h3 {
  font-weight: 400; }

h2 {
  cursor: pointer;
  font-size: 24px;
  line-height: 135%;
  margin: 10px 0; }

.playfair {
  font-family: 'Playfair Display', serif;
  font-size: 20px; }

.code {
  font-family: 'VT323', sans-serif; }

.josefin {
  font-family: 'Voltaire', serif; }

.amatic {
  font-family: 'Amatic SC', serif; }

.pulse {
  margin: 100px;
  display: block;
  width: 22px;
  height: 22px;
  border-radius: 50%;
  background: #cca92c;
  cursor: pointer;
  box-shadow: 0 0 0 rgba(204, 169, 44, 0.4);
  animation: pulse 2s infinite; }

.pulse:hover {
  animation: none; }

@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(204, 169, 44, 0.4); }
  70% {
    box-shadow: 0 0 0 10px rgba(204, 169, 44, 0); }
  100% {
    box-shadow: 0 0 0 0 rgba(204, 169, 44, 0); } }

.bulbs {
  background-image: url("../images/bulbs.png");
  background-position: right top;
  color: #f5f5f5; }

.constellation {
  background-image: url("../images/constellation.png"); }

.mac {
  background-image: url("../images/mac.png"); }

.mars {
  background-image: url("../images/mars.png");
  background-position: center center;
  color: #f5f5f5; }

.mountains {
  background-image: url("../images/mountains.png");
  background-position: right bottom;
  color: #f5f5f5; }

.satellites {
  background-image: url("../images/satellites.png"); }

.underwater {
  background-image: url("../images/underwater.png");
  color: #f5f5f5; }

.unicorn {
  background-image: url("../images/unicorn.png");
  background-position: right bottom; }

.wild {
  background-image: url("../images/wild.png");
  background-position: center center; }

.trophy-case {
  align-items: center;
  display: flex;
  justify-content: center;
  opacity: 0.5;
  padding-bottom: 15px;
  position: fixed;
  top: 0;
  transition: all 0.8s cubic-bezier(0.22, 0.61, 0.36, 1);
  transform: translateY(calc(-100% + 20px));
  width: 100%; }
  .trophy-case:hover {
    opacity: 1;
    transform: translateY(0); }
  .trophy-case .trophy {
    color: #ffffff;
    display: none;
    justify-content: center;
    opacity: 0.8;
    padding: 15px;
    pointer-events: none;
    text-decoration: none;
    transition: all 0.5s ease-out;
    width: 10%; }
    .trophy-case .trophy.polished {
      background: #000000;
      color: #bbbbbb;
      cursor: pointer;
      display: flex;
      pointer-events: all; }
      .trophy-case .trophy.polished:hover {
        background: #663399;
        color: white;
        opacity: 1; }
    .trophy-case .trophy::before {
      font-size: 32px; }

.font-selection__intro {
  margin: 10px 0;
  font-size: 14px;
  text-transform: uppercase;
  font-family: sans-serif; }

.font-selection__item {
  background-color: #161616;
  color: #e5e5e5;
  cursor: pointer;
  opacity: 0.7;
  padding: 0 20px;
  position: relative;
  transition: opacity 0.5s linear; }
  .font-selection__item::before, .font-selection__item::after {
    border: 5px solid #f2c341;
    border-radius: 5px;
    content: '';
    display: block;
    height: 10px;
    opacity: 0;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    transition: 0.3s all linear;
    width: 10px; }
  .font-selection__item::before {
    left: 0; }
  .font-selection__item::after {
    right: 0; }
  .font-selection__item:hover {
    opacity: 1; }
  .font-selection__item.selected {
    opacity: 1; }
    .font-selection__item.selected::before, .font-selection__item.selected::after {
      opacity: 1; }

.footer-menu {
  background: #f5f5f5;
  border-top: 1px solid #333333;
  bottom: 0;
  color: #333333;
  position: fixed;
  width: 100%;
  left: 0;
  right: 0; }
  .footer-menu small {
    display: block;
    font-size: 12px; }

.article {
  align-content: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  margin: 0 auto;
  max-width: 675px;
  overflow: auto;
  text-align: justify;
  width: 80%; }
  .article em {
    cursor: pointer;
    font-style: normal;
    opacity: 1;
    text-decoration: underline; }
  .article .exhausted {
    cursor: default;
    font-style: italic;
    text-decoration: none; }
  .article .highlight {
    background: #b38cd9;
    opacity: 0.8;
    transition: background 0.4s linear, color 0.4s linear, opacity 0.2s linear; }
    .article .highlight.revealed {
      background: transparent;
      opacity: 1; }
  .article.three::first-letter {
    font-size: 64px; }

.flex {
  display: flex !important; }

.flex--col {
  flex-direction: column; }

.hidden {
  display: none !important;
  opacity: 0.7; }

.selected {
  opacity: 1; }

.pointed {
  cursor: pointer; }
